* {
  font-family: "Oswald";
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

section {
  margin-bottom: 60px;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.background {
  position: relative;
  height: 100vh;
  background: url(../img/IMAGE.png) fixed;
  background-size: cover;
}

header {
  position: fixed;
  background-color: rgba(19, 19, 22, 0.7);
  width: 100%;
  z-index: 1;
}

.container {
  width: 1170px;
  margin: auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

h1 a {
  font-size: 30px;
  color: white;
  text-align: left;
}

.subtitle {
  font-size: 9px;
  letter-spacing: 6px;
}

.nav_menu {
  display: flex;
}

.nav_menu li {
  margin-left: 50px;
}

.nav_menu li:first-child {
  margin-left: 0;
}

.menu_link {
  font-size: 17px;
  text-transform: uppercase;
  color: white;
}

.menu_link:hover {
  color: #ffe600;
  border-bottom: 3px solid white;
}

.slider {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.7);
}

.slider_info {
  text-align: center;
  margin: auto;
}

.slider h2 {
  font-size: 60px;
  color: white;
}

.btn {
  display: inline-block;
  background-color: #ffe600;
  padding: 10px 20px;
  margin: 20px;
}

.btn a {
  font-size: 20px;
  font-weight: 700;
  color: #131316;
}

.btn:hover {
  outline: 1px solid white;
}

.content {
  text-align: center;
  margin-bottom: 60px;
}

.our_content {
  font-size: 16px;
  font-weight: 400;
  color: #515151;
  margin-bottom: 25px;
}

.content_title {
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 25px;
}

.title_line {
  border: 1px solid #ffe600;
  width: 170px;
  margin: auto;
}

.content_description {
  margin-top: 20px;
  font-size: 16px;
  color: #707070;
}

.servise {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.service_box {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 25%;
  margin: 0 -10px;
}

.service_img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  background: #ffe600;
  border-radius: 50%;
}

.service_title {
  margin-top: 20px;
  font-size: 20px;
  font-weight: 700;
}

.service_description {
  margin-top: 20px;
  font-size: 16px;
  color: #707070;
  padding: 0 10px;
}
